import React, { Component } from 'react'
import { Modal } from 'antd'

export default class EventsModal extends Component {
  render() {
    const { toggleModal, visible } = this.props;

    return (
      <Modal
        centered
        visible={visible}
        onOk={() => toggleModal(false)}
        onCancel={() => toggleModal(false)}
      >
        TEST MODAL
      </Modal>
    )
  }
}
